import { Injectable } from '@angular/core';
import { Pair } from '../model/pair.interface';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  private subject = new BehaviorSubject<Array<Pair>>([]);
  private _level = 0;
  

  public get observable$(){
    return this.subject.asObservable();
  };
  
  public get level(){
    return this._level + 1;
  }

  constructor() {
    this.next();
   }

   



   private generateData2(level){
    let _data = [];
    let multi = (level<3)? 10: (10 * (level - 2));
    for(let idx = 0 ; idx < 5 ; ++idx){
      let a = Math.trunc(Math.random() * multi);
      let b = Math.trunc(Math.random() * multi);      
      let op = Math.trunc(Math.random()*(level + 1)) % 3;
      
      let newPair;
      if(op === 0){
        newPair  = {id: idx, a:`${a}+${b}`, b: `${a+b}`, match:false};
      } else if(op === 1){
        newPair  = {id: idx, a:`${a}-${b}`, b: `${a-b}`, match:false};
      }
      else if(op === 2){
        newPair  = {id: idx, a:`${a}*${b}`, b: `${a*b}`, match:false};
      } else {
        console.log("EEEEEEE");
      }

      // Result alrady exist, retry
      if( _data.find( pair => pair.b === newPair.b)){
        --idx;        
      } else {
        _data.push(newPair);
      }
    }
    this.subject.next([..._data]);
   }



   public next() {      
      this.generateData2(this._level);
      this._level++;  
   }


}
