import { Component, OnInit, ViewChild, ElementRef, AfterViewInit, OnDestroy, ViewChildren, ContentChild, AfterContentInit, Input, TemplateRef } from '@angular/core';
import { DataService } from 'src/app/common/data.service';
import { Observable, Subscription, fromEvent } from 'rxjs';
import { Pair } from 'src/app/model/pair.interface';
import { Éµangular_packages_platform_browser_platform_browser_d } from '@angular/platform-browser';
import { IAppTimer, TimerComponent } from 'src/app/share/timer/timer/timer.component';

@Component({
  selector: 'app-game1',
  templateUrl: './game1.component.html',
  styleUrls: ['./game1.component.css']
})
export class Game1Component implements OnInit, AfterViewInit, OnDestroy, AfterContentInit {  
  
  // Template as input
  @Input('scoreTableTpl1')
  scoreTableTpl1: TemplateRef<any>

  // Template as input
  @Input('scoreTableTpl2')
  scoreTableTpl2: TemplateRef<any>

  // Template as content projection
  @ContentChild('templateAsContentProjection', {static:false})
  templateAsContentProjectionTplRef:TemplateRef<any>;
  
  public get match (){
    return this._match;
  }
  public get noMatch() {
    return this._noMatch;
  } 

  
  private pairs$: Observable<Pair[]>;
  private _aId =  -1;
  private _bId = -1;
  private _match = false;
  private _noMatch = false;
  private _matchsCnt = 0;
  private _tickSubscription: Subscription;
  

  public bestTime = 0;
  public missed = 0;
  public context = {
    bestTime: this.bestTime,
    missed: this.missed
  };
  
  constructor(private dataService: DataService) { 
  }

  ngOnInit() {
    this.pairs$ = this.dataService.observable$;   
  }

  ngAfterViewInit(): void {    
  }

  ngOnDestroy(): void {
    this._tickSubscription.unsubscribe();
  }


  // ***********************************************************
  //              Content Projection
  // ***********************************************************
  @ContentChild(TimerComponent, {static:false}) 
  appTimer: IAppTimer
  ngAfterContentInit(): void {    
    // ---------------------------------------------------------------------
    // Manually Register to event from content projection
    // ---------------------------------------------------------------------
    this._tickSubscription = this.appTimer.tickEvent$.subscribe( (tick) => {
      //console.log(`[game1 (subscription to EventEmtiter)] tick from content`, tick); 
    });
    // ---------------------------------------------------------------------
    // API of the content projection
    // ---------------------------------------------------------------------
    this.appTimer.start(true);
  }

  public clickFromTemplate(){
    console.log('[game1] clickFromTemplate');
  }

  public tableScore = 0;
  public toggleScore(){
    ++this.tableScore;
  }


  public handleA(pair: Pair){
    this.handleClick(pair);
  }

  public handleB(pair: Pair){
    this.handleClick(undefined, pair);
  }
  
  public isItemASelected(id){
    return this._aId === id;
  }
  public isItemBSelected(id){
    return this._bId === id;
  }

  private handleClick(a:Pair = undefined, b:Pair = undefined){
    if(a){
      if(this._aId === -1  || this._aId !== a.id ){
        this._aId = a.id;        
      } else {
        this._aId = -1;
      }
    }
    if(b){
      if(this._bId === -1 || this._bId !== b.id ){
        this._bId = b.id;        
      } else {
        this._bId = -1;
      }
    }

    this.checkMatch(a || b)

  }

  
  private checkMatch(pair: Pair) {
    // Match
    if(this._aId !== -1 && this._aId === this._bId){
      pair.match = true;
      this._match = true;
      setTimeout( ()=> this._match = false, 250);
      this._aId = -1;
      this._bId = -1;
      this._matchsCnt++;
      if(this._matchsCnt === 5){
        this.appTimer.stop();       
        if(this.bestTime === 0) {
          this.bestTime = this.appTimer.elapsed 
        }
        this.bestTime = Math.min(this.bestTime, this.appTimer.elapsed);
        setTimeout( _ => {
          this.dataService.next();
          this._matchsCnt = 0;
          this.appTimer.start(true);
        }, 3000);
      }
      // Missed
    } else if((this._aId !== -1) && (this._bId !== -1) && (this._aId !== this._bId)){
      this._noMatch = true;
      setTimeout( ()=> this._noMatch = false, 250);
      this._aId = -1;
      this._bId = -1;
      this.missed++;
    }
  }

  public tick2(tick){
    console.log(`[timer] tick from content`, tick);  // We don't get it
  }

}
