import { Component, OnInit, Output, EventEmitter, ViewChild, AfterViewInit, ElementRef } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { TestDirective } from '../../test/test.directive';



export interface IAppTimer {
  start (reset: boolean): void;
  stop(): void;
  reset(): void;  
  elapsed: number;                  // Getter: get elapsed() {  return ...}
  tickEvent$: Observable<any>;      // Getter: Communication from timer to the component hosting the ng-content
}


@Component({
  selector: 'app-timer',
  templateUrl: './timer.component.html',
  styleUrls: ['./timer.component.css']
})
export class TimerComponent implements OnInit, IAppTimer, AfterViewInit {
 
  public get elapsed(): number {
    return this._elapsed;
  }

  public get tick$() {
    return this._tickSubject.asObservable(); // for compoent host to get the event
  }

  public get tickEvent$() {
    return this.tickEvent.asObservable();  // Communication from timer to the component hosting the ng-content
  }

  @Output('tick')
  public tickEvent = new EventEmitter();   // Simple Angular event register in HTML


  // A way to get hold of the TestDirective
  @ViewChild(TestDirective, {static:false})
  testDirective: ElementRef; // or testDirective: TestDirective


  private _elapsed = 0;
  private _setIntervalHandler;
  private _tickSubject = new BehaviorSubject<number>(0);
  
  constructor() {   
  }

  ngOnInit() {
  }

  ngAfterViewInit(): void {
    console.log('[timer] ngAfterViewInit:testDirective', this.testDirective);
  }



  public start(reset: boolean){
    if(!this._setIntervalHandler){
      if(reset){
        this.reset();
      }
      this._setIntervalHandler = setInterval( () => {
        ++this._elapsed;
        this.setElapsed(this._elapsed);
      }, 1000);
    }
  }

  public stop(){
    if(this._setIntervalHandler){
      clearInterval(this._setIntervalHandler);
      this._setIntervalHandler = null;
    }
  }

  public reset(){
    this.setElapsed(0);
  }

  private setElapsed(val:number) {
    this._elapsed = val;
    this.tickEvent.next(val);       // Simple Angular event 
    this._tickSubject.next(val);    // for compoent host to get the event
  }


}
